html {
    font-size: 100%;
}

.red {
    --main-color: red;
}

.blue {
    --main-color: blue;
}

.green {
    --main-color: green;
}

.container-1 {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.box {
    display: flex;
    width: 15%;
    height: 100px;
    padding: 15px;
    margin: 5px;
    border-radius: 5px;
    color: rgb(87, 87, 87);
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
}

.base-color {
    background-color: var(--main-color);
    color: white;
    width: 20%;
}

@for $i from 1 through 4 {
    $main-color: red;
    .darken-#{$i} {
        background-color: darken($main-color, $i * 7%);
    }
    .lighten-#{$i} {
        background-color: lighten($main-color, $i * 7%);
    }
}